import axios from 'axios'

const request = axios.create({
    baseURL: 'http://8090',
    timeout: 5000,
});

// 请求拦截器 
request.interceptors.request.use(
    config => {
        // 在发送请求之前可以做一些处理
        return config;
    },
    error => {
        // 处理请求错误
        console.log(error);
        return Promise.reject(error);
    }
);

// 响应拦截器 
request.interceptors.response.use(
    response => {
        // 在这里可以对响应数据做一些处理
        return response;
    },
    error => {
        // 处理响应错误
        console.log(error);
        return Promise.reject(error);
    }
);

export default request;



